"use client";

import { useEffect, useState } from "react";
import WOW from "wowjs";
import MobsterClient from "../components/MobsterClient";

export default function Home() {
  useEffect(() => {
    new WOW.WOW().init(); // Initialize WOW.js
  }, []);

  const egitimResimleri = ["/img/egitimler.png", "/img/egitimler_2.png"];

  const [egitimResmi, setEgitimResmi] = useState(egitimResimleri[0]);

  useEffect(() => {
    const interval = setInterval(() => {
      setEgitimResmi((oncekiResim) => {
        const currentIndex = egitimResimleri.indexOf(oncekiResim);
        const sonrakiIndex = (currentIndex + 1) % egitimResimleri.length;

        return egitimResimleri[sonrakiIndex];
      });
    }, 3000);

    return () => clearInterval(interval);
  }, []);

  return (
    <>

      <MobsterClient></MobsterClient>
    </>
  );
}
